















import Vue from "vue";
import {groomedQuestDataSet} from "@/types/types";

export default Vue.extend({
  name: "QuestLog",
  computed: {
    quests() : groomedQuestDataSet {
      return this.$store.state.quests.data
    }
  },
  mounted() : void {
    this.$store.dispatch('quests/getQuestData')
  }
});
